.footer {
  position: sticky;
  z-index: -1;
  bottom: 0;

  display: block;
  box-sizing: border-box;
  height: 200px;

  background-image: url("../images/footer.png");
  background-size: cover;

  @include onTablet {
    height: 480px;
  }

  @include onDesktop {
    height: 555px;
  }
}
